.animaBalls {
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
}

.item-1 {
    width: 100px;
    height: 100px;
    position: absolute;
    animation: right-1 4s infinite cubic-bezier(0, 0, 0.49, 1.02);
    animation-delay: 100ms;
    display: flex;
    justify-content: center;
    align-items: center;
}

.item-1>* {
    width: 20px;
    height: 20px;
    background: #F583A1;
    border-radius: 50%;
    background-color: #E8DDB5;
}

@keyframes right-1 {
    10% {
        transform: rotate(250deg) translate(0, 0px);
    }

    50%,
    65% {
        transform: rotate(250deg) translateX(-100px);
    }

    100% {
        transform: rotate(250deg) translate(0, 0px);
    }
}

